.click-type .unit-click {
	width: auto;
	padding: 12px 10px;
	text-align: center;
	border-radius: 14px;
	border: 1px solid #f5d0a3;
	box-shadow: inset 0px -3px 0px 0px #f0b46c;
	-webkit-box-shadow: inset 0px -3px 0px 0px #f0b46c;
	-moz-box-shadow: inset 0px -3px 0px 0px #f0b46c;
	-ms-box-shadow: inset 0px -3px 0px 0px #f0b46c;
	background-color: #ffebc0;
	box-sizing: border-box;
}

.click-type .unit-click.click-o {
	position: relative;
	border: 1px solid #ffebc0;
	box-shadow: inset 0px -3px 0px 0px #ffebcf;
	-webkit-box-shadow: inset 0px -3px 0px 0px #ffebcf;
	-moz-box-shadow: inset 0px -3px 0px 0px #ffebcf;
	-ms-box-shadow: inset 0px -3px 0px 0px #ffebcf;
	box-sizing: border-box;
}

.click-type .unit-click.click-o::after {
	display: block;
	position: absolute;
	left: 50%;
	top: 50%;
	width: 42px;
	height: 42px;
	margin-top: -21px;
	margin-left: -21px;
	background-image: url("../../../images/i-correct@2x.png");
	background-repeat: no-repeat;
	background-size: 42px 42px;
	content: '';
	animation-name: pulse;
	-webkit-animation-name: pulse;
	animation-duration: 0.8s;
	-webkit-animation-duration: 0.8s;
}

@keyframes pulse {
	0% {
		transform: scale(1);
		opacity: 1;
	}
	50% {
		transform: scale(1.4);
		opacity: 0.7;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}

@-webkit-keyframes pulse {
	0% {
		transform: scale(1);
		opacity: 1;
	}
	50% {
		transform: scale(1.4);
		opacity: 0.7;
	}
	100% {
		transform: scale(1);
		opacity: 1;
	}
}

.click-type .unit-click.click-x {
	position: relative;
	border: 1px solid #f7f7f7;
	box-shadow: inset 0px -3px 0px 0px #f7f7f7;
	-webkit-box-shadow: inset 0px -3px 0px 0px #f7f7f7;
	-moz-box-shadow: inset 0px -3px 0px 0px #f7f7f7;
	-ms-box-shadow: inset 0px -3px 0px 0px #f7f7f7;
	background-color: #f7f7f7;
