.connect-type .connect-box {
	display: table;
	width: auto;
	padding: 6px 0;
	text-align: center;
	vertical-align: middle;
	box-sizing: border-box;
}

.connect-type .connect-area {
	position: static;
	width: auto;
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

.connect-type .connect-area-inner {
	position: relative;
	padding: 19px 0;
	box-sizing: border-box;
	font-size: 0;
	box-sizing: border-box;
}

.connect-type .ca-box {
	display: table-cell;
	height: 200px;
	padding: 10px;
	border-radius: 20px;
	vertical-align: middle;
	box-sizing: border-box;
}

.connect-type .ca-box.box-short {
	width: 272px;
	border: 2px solid #e9e5e2;
	background-color: #fff;
	box-sizing: border-box;
}

.connect-type .ca-box.box-long {
	width: 576px;
	background-color: #fff6e7;
}

.connect-type .ca-box.box-long .txt {
	font-size: 24px;
	line-height: 1.5em;
}

.connect-type .connect-point {
	position: static;
	width: auto;
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

.connect-type .connect-point.cp-left {
	padding-right: 64px;
	box-sizing: border-box;
}

.connect-type .connect-point.cp-right {
	padding-left: 64px;
	box-sizing: border-box;
}

.connect-type .connect-point-inner {
	position: relative;
	padding: 19px 0;
	box-sizing: border-box;
	font-size: 0;
}

.connect-type .connect-dot-wrap {
	display: table-cell;
	height: 200px;
	padding: 0 14px;
	border-radius: 20px;
	vertical-align: middle;
	box-sizing: border-box;
}

.connect-type .connect-dot-wrap .connect-dot {
	position: relative;
	display: inline-block;
	width: 24px;
	height: 24px;
	border: 6px solid #f5cc69;
	border-radius: 14px;
	background-color: #fdf5e1;
	box-sizing: border-box;
}

.popup {
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 30;
}

.popup.on {
	display: block;
}

.popup .dim {
	display: block;
	width: 100%;
	height: 100%;
	background-color: #000;
	opacity: 0.7;
}

.popup .dp-table {
	position: absolute;
	top: 0;
	left: 0;
}

.popup .king-info-box {
	position: relative;
	display: inline-block;
	width: auto;
	height: auto;
	min-width: 1160px;
	padding-bottom: 36px;
	background-color: #fff;
	border-radius: 20px;
}

.popup .king-info-box h1 {
	position: relative;
	display: block;
	margin-top: 22px;
	margin-bottom: 26px;
	padding-left: 70px;
	text-align: left;
}

.popup .king-info-box h1:before {
	position: absolute;
	top: 4px;
	left: 28px;
	display: inline-block;
	width: 34px;
	height: 34px;
	background-image: url("../../../images/popup/i-title-info.png");
	background-position: 0, 0;
	background-size: 34px 34px;
	background-repeat: no-repeat;
	content: '';
}

.popup .king-info-box h1 .txt {
	position: relative;
	margin-left: 6px;
	padding-left: 12px;
	font-size: 18px;
	font-weight: 600;
	color: #000;
	letter-spacing: -0.07em;
}

.popup .king-info-box h1 .txt:before {
	position: absolute;
	top: -4px;
	left: 0;
	display: block;
	width: 1px;
	height: 30px;
	background-color: #cccccc;
	content: '';
}

.popup .king-info-box .btn-close {
	position: absolute;
	top: 8px;
	right: 6px;
}

.popup .king-info-box .btn-close button {
	display: inline-block;
	padding: 10px;
	background-color: transparent;
}

@media only screen and (max-width: 1160px) {
	.popup .study-info-box {
		min-width: auto;
		min-height: auto;
	}
	.pop-img img {
		width: 100%;
		height: 100%;
	}
}
