// * placeholder
@include placeholder-support {
	input[placeholder] {
		line-height: 1; }

	input {
		&::-webkit-input-placeholder {
			//Chrome, Opera, Safari
			text-align: $placeholder-text-align;
			color: $placeholder-color; }
		&::-moz-placeholder {
			// Firefox 19+
			text-align: $placeholder-text-align;
			color: $placeholder-color; }
		&:-moz-placeholder {
			text-align: $placeholder-text-align;
			color: $placeholder-color; }
		&:-ms-input-placeholder {
			// IE 10+
			text-align: $placeholder-text-align;
			color: $placeholder-color; }
		&:focus::-webkit-input-placeholder {
			opacity: 0; }
		&:focus::-moz-placeholder {
			opacity: 0; }
		&:focus::-ms-input-placeholder {
			opacity: 0; } } }

// * table
// table
// 	border-collapse: collapse
// 	border-spacing: 0
@include table-support {
	table {
		border-collapse: collapse;
		border-spacing: 0; } }

// table
// 	@extend %reset-table
#skip, .blind, legend, caption {
	@extend %blind; }
